@import url(https://fonts.googleapis.com/css2?family=Mountains+of+Christmas&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Elsie&family=Mountains+of+Christmas&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Elsie&family=Milonga&family=Mountains+of+Christmas&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mountains+of+Christmas&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Elsie&family=Mountains+of+Christmas&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Elsie&family=Milonga&family=Mountains+of+Christmas&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mountains+of+Christmas&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Elsie&family=Mountains+of+Christmas&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Elsie&family=Milonga&family=Mountains+of+Christmas&display=swap);
ul {
  display: flex;
  list-style: none;
}

.mealslist-heading li,
.mealslist-heading select {
  padding-right: 10px;
  margin-right: 50px;
  width: 59px;
  height: 16px;
  font-family: 'Milonga', cursive;
  font-size: 13px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: 1.9px;
  color: #121212;
  cursor: pointer;
}

.mealslist-heading select {
  width: 140px;
  padding-left: 3px;
  font-weight: bold;
  background-color: #d57d1f;
}

.mealslist-heading li:first-child {
  width: 250px;
  height: 37px;
  font-family: 'Mountains of Christmas', cursive;
  font-size: 30px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #008000;
  padding: 2px;
  margin: 0;
}

.header {
  margin-top: 5%;
  display: flex;
  justify-content: space-between;
  padding-right: 100px;
  padding-left: 60px;
  border-bottom: 1px solid #e8e8e8;
  padding-bottom: 18px;
  background-color: #d57d1f;
  z-index: 100000;
}

.content {
  background-color: #000;
  margin-top: -35px;
}

.header button {
  width: 160px;
  font-family: 'Elsie', cursive;
  font-size: 13px;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: 0.5px;
  text-align: center;
  color: #fff;
  height: 33px;
  margin-right: 190px;
  border-radius: 3px;
  background-color: #2d2013;
  cursor: pointer;
  border: 1px solid #555;
  padding: 3px;
}

.meal-row {
  height: 170px;
  padding: 32px 119px 26px 27px;
  margin-top: 40px;
  display: flex;
  justify-content: space-evenly;
  margin-right: 100px;
  margin-left: 100px;
  border-radius: 4px;
  border: solid 1px #e8e8e8;
  background-color: #d57d1f;
}

.category {
  width: 49px;
  height: 18px;
  margin: 0 39px 0 0;
  opacity: 0.5;
  font-family: 'Milonga', cursive;
  font-size: 14px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #121212;
}

.title {
  width: 197px;
  height: 29px;
  margin: 3px 323px 0 0;
  font-family: 'Elsie', cursive;
  font-size: 22px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: -0.2px;
  color: #121212;
}

.title-category,
.image-area .right {
  width: 250px;
}

.image-area {
  height: 150px;
  width: 150px;
  border: solid 3px #2d2013;
}

a {
  text-decoration: none;
}

.tags {
  margin-left: 53px;
  opacity: 0.5;
  font-family: 'Milonga', cursive;
  font-size: 14px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #121212;
}

.meal-row button {
  font-family: 'Elsie', cursive;
  font-size: 13px;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: 0.5px;
  text-align: center;
  color: #fff;
  width: 160px;
  height: 33px;
  margin: 12px 0 9px 59px;
  border-radius: 3px;
  background-color: #2d2013;
  border: #2d2013;
  cursor: pointer;
  padding: 1px;
  border-right: 1px solid #8b8b8b;
  border-left: 1px solid #fff;
  border-bottom: 1px solid #fff;
}

.meal-row button:hover {
  border: 2px outset #def;
  color: #121212;
  background-color: #f8f8f8 !important;
}

.hidden {
  visibility: hidden;
}

.highlighted {
  background-color: yellow;
}

.no-meals {
  color: #fff;
  font-family: 'Milonga', cursive;
  text-align: center;
  height: 40px;
}

h2 {
  text-align: center;
  font-family: 'Mountains of Christmas', cursive;
  color: #2d2013;
}

.list-by-alphabet {
  background-color: #d57d1f;
  margin: 10%;
  border: 3px solid #2d2013;
  padding: 5%;
}

.list-by-alphabet .ul {
  list-style: none;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.ul li {
  padding: 8px;
  font-family: 'Elsie', cursive;
}

a {
  text-decoration: underline;
  color: #008000;
}

.image.details-pg {
  display: flex;
  margin-top: 5%;
  justify-content: center;
}

.image-area.details-pg {
  height: 300px;
  width: 300px;
}

.instructions {
  font-family: 'Milonga', cursive;
}

.meal-row.details-pg {
  height: 100px;
}

.meal-row.ins {
  height: 500px;
}

.ins-heading {
  display: flex;
  justify-content: space-between;
}

.body-ins {
  font-family: 'Milonga', cursive;
}

.details-bg {
  background-color: #000;
}

